@-webkit-keyframes rotate {
    from {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    to {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}

@keyframes rotate {
    from {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    to {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}

#app--wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-direction: column;
        flex-direction: column;
}

.router--wrapper {
}

.router--wrapper.transparent {
    opacity: 0;
}



.transform-on-hover {
    -webkit-transition: background-color 0.1s linear, -webkit-transform 0.1s;
    transition: background-color 0.1s linear, -webkit-transform 0.1s;
    -o-transition: transform 0.1s, background-color 0.1s linear;
    transition: transform 0.1s, background-color 0.1s linear;
    transition: transform 0.1s, background-color 0.1s linear, -webkit-transform 0.1s;
}

.transform-on-hover:hover {
    -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
            transform: scale(1.2);
}

html {
    overflow: hidden;
    width: 100vw;
    height: 100vh;
}

body {
    position: absolute;
}

.blog-link--wrapper:hover {
    background-color: rgba(255, 255, 255, 0.3)!important;
}

@media (max-width: 700px) {
    html {
        font-size: 10px;
    }
}

/* markdown */
.markdown-parser--wrapper.tail:after {
    content: "";
    display: block;
    min-height: 50px;
    width: 100%;
}

.markdown-parser--wrapper > p:first-child > img {
    margin-top: -1.5rem!important; /* override inline css from parser */
}



